body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.home {
  border: solid black;
  margin: 2em 20%;
  padding: 2em;
}

.home-header h1 {
  margin-top: 0;
  margin-bottom: 1em;
  padding: 0;
}

.header-description {
  margin: 20px 0;
  font-size: 18px;
}

.home nav {
  margin-top: 1em;
  margin-bottom: 2em;
  text-align: center;
  font-size: 18px;
  font-weight: bold;
}

.home-visuals {
  margin-top: 2em;
  margin-bottom: 2em;
}

.visual {
  margin: 1em 0 2em;
}

.visual a {
  color: #9900ff;
}

.visual-img {
  width: 100%;
  height: auto;
  opacity: 1;
  transition: opacity 0.5s;
}

.img-container {
  background-color: #222222;
}

.visual-img:hover {
  opacity:0.8;
}

@media only screen and (max-width: 600px) {
  .home {
    margin: 1em 1em;
    padding: 1.5em;
  }
}
.about {
  border: solid black;
  margin: 2em 20%;
  padding: 2em;
}

.about-header h1 {
  margin-top: 0;
  margin-bottom: 1em;
  padding: 0;
}

.about-sub {
  margin-bottom: 0;
  /* text-decoration: underline; */
}

.about-description {
  margin: 20px 0;
  font-size: 18px;
}

.about nav {
  margin-top: 1em;
  margin-bottom: 2em;
  text-align: center;
  font-size: 18px;
  font-weight: bold;
}

.about-visuals {
  margin-top: 2em;
  margin-bottom: 2em;
}

.visual {
  margin: 1em 0 2em;
}

.visual h4 {
  color: #9900ff;
}

.visual-tag {
  list-style-type: none;
  margin-bottom: 5em;
  padding: 0;
}

.visual-tag li {
  float: left;
  padding: 4px;
  margin-right: 5px;
  border-radius: 15px;
  font-weight: bold;
  color: whitesmoke;
  background-color: #444444;
}

@media only screen and (max-width: 600px) {
  .about {
    margin: 1em 1em;
    padding: 1.5em;
  }
}
